<template>
  <div class="content">
    <ul class="n-list">
      <li class="n-item" v-for="item in noticeList" :key="item.article_id"
      @click="todetails(item.article_id)">
        <img :src="item.thumb" :alt="item.title" />
        <div class="info">
          <div class="text">
            <span class="tag">{{ item.tag }}</span><span class="title">{{ item.title }}</span>
          </div>
          <div class="time">{{ item.publish_time }}</div>
        </div>
        
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      noticeList: [
        {
          title: "悦享美微商城今日正式上线啦~悦享美微商城今日正式上线啦~",
          article_id: 1,
          cat_id: 2,
          publish_time: "2019-07-08 12:20:56",
          tags: "3",
          thumb:require('@/assets/homepages/img5.png'),
          cat_name: "商城头条",
          tag: "精品"
        },
        {
          title: "悦享美微商城今日正式上线啦~悦享美微商城今日正式上线啦~",
          article_id: 2,
          cat_id: 2,
          publish_time: "2019-07-08 12:20:56",
          tags: "1",
          thumb:require('@/assets/homepages/img5.png'),
          cat_name: "商城头条",
          tag: "热门"
        },
         {
          title: "悦享美微商城今日正式上线啦~悦享美微商城今日正式上线啦~",
          article_id: 3,
          cat_id: 2,
          publish_time: "2019-07-08 12:20:56",
          tags: "1",
          thumb:require('@/assets/homepages/img5.png'),
          cat_name: "商城头条",
          tag: "热门"
        }

      ]
    };
  },
  methods:{
    todetails(id){
      this.$router.push({
        name:'details',
        params:{
          id:id
        }
      })
      // this.$routr.params = {
      //   id:id
      // }
    }
  }
};
</script>

<style lang="scss" scoped>
.content{
  .n-list{
    .n-item{
      background-color: #FFF;
      height: 260px;
      display: flex;
      margin-bottom: 20px;
      // align-items: center;
      box-sizing: border-box;
      padding: 20px 24px 20px 20px;
      img{
        flex:none;
        width: 220px;
        height: 220px;
      }
      .info{
        padding: 20px 0 0 14px;
        .text{
          height: 100px;
          color: #222;
          font-size: 28px;
          line-height: 40px;
          .tag{
            border: thin solid #FF4A54;
            color: #FF4A54;
            font-size:22px; 
            padding: 3px 7px;
            border-radius: 3px;
            margin-right:15px;
            
          }
          .title{
            line-height: 45px;
          }
        }
        .time{
          color: #888;
          font-size: 24px;
          margin-top: 20px;
          padding-left: 10px;
        }
      }
    }
  }
}
</style>



